body {
  background: url("/src/images/background.png") center center;
  background-color: #000000;
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

.logo {
  height: 150px;
}

:root {
  --border-color: rgb(255, 255, 255, 0.1);
}

nav {
  display: flex;
  width: 100%;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.643);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

nav a {
  text-decoration: none;
  color: inherit;
  font-weight: 400;
  font-size: larger;
}

.links-section {
  display: flex;
  gap: 65px;
  box-sizing: border-box;
  margin: auto;
  margin-right: 170px;
}

.links-section a:hover {
  border-bottom: 1px solid white;
}

main {
  flex-grow: 1;
  margin: auto;
  width: 1500px;
  padding-top: 100px;
  padding-bottom: 100px;
}

.main {
  background-color: #0000004d;
}
main > article {
  display: grid;
  grid-template-areas:
    "clothes1-data clothes1-data clothes1"
    "brand brand brand "
    "clothes2 clothes2-data clothes2-data";
  grid-gap: 30px;
}

main > article > .main_content {
  height: 650px;
}

.clothes1-data {
  grid-area: clothes1-data;
}
.clothes1 {
  grid-area: clothes1;
}
.clothes2-data {
  grid-area: clothes2-data;
}
.clothes2 {
  grid-area: clothes2;
}
.brand {
  grid-area: brand;
}

.clothes1,
.clothes1-data {
  border: 2px solid white;
  box-shadow: 0 0 10px white;
  transition: all 0.2s ease;
}

.clothes1-data:hover,
.clothes1:hover {
  border: 2px solid white;
  box-shadow: 0 0 20px rgba(0, 0, 255, 0.7);
  animation: glow 1.5s infinite alternate;
}

.clothes2,
.clothes2-data {
  border: 2px solid white;
  box-shadow: 0 0 10px white;
  transition: all 0.2s ease;
}

.clothes2-data:hover,
.clothes2:hover {
  border: 2px solid white;
  box-shadow: 0 0 20px rgba(0, 0, 255, 0.7);
  animation: glow 1.5s infinite alternate;
}
.brand {
  border: 2px solid white;
  box-shadow: 0 0 10px white;
  transition: all 0.2s ease;
}

.brand:hover {
  border: 2px solid white;
  box-shadow: 0 0 20px rgba(0, 0, 255, 0.7);
  animation: glow 1.5s infinite alternate;
}
